AW;IACX,aAAa;IACb,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,UAAU;IACV,YAAY;IACZ,eAAe;AACnB;AAEA;IACI,aAAa;AAEjB;AAEA;IACI,WAAW;IACX,aAAa;IACb,YAAY;IACZ,yBAAyB;IACzB,kBAAkB;IAClB,UAAU;IACV,eAAe;AACnB;AAEA;IACI,aAAa;IACb,eAAe;AACnB;AAEA;IACI,yBAAyB;IACzB,WAAW;IACX,kBAAkB;IAClB,iBAAiB;IACjB,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;AACA;IACI,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,cAAc;IACd,iBAAiB;IACjB,kBAAkB;IAClB,iBAAiB;IACjB,YAAY;IACZ,uBAAuB;AAC3B;AACA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;IACd,kBAAkB;IAClB,qBAAqB;IACrB,mBAAmB;IACnB,eAAe;IACf,YAAY;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from '@vue/reactivity';\nimport { ref, watch } from 'vue'\nimport { container } from '../../../../domain/core/diContainer';\nimport { IApplication } from \"../../../../domain/model/models\"\nimport { IIDEInputDefinitions } from '../../../../domain/presentation/IIDEInputDefinitions';\nimport { IApplicationUseCase } from '../../../../domain/useCase/IApplicationUseCase';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport IDEInput from \"../../components/application/IDEInput.vue\";\ninterface IAddOrUpdateAppPropsInline {\n    app?: IApplication;\n    templateName?: string;\n    organizationGroupId?: string;\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst applicationUseCase = container.resolve<IApplicationUseCase>(IApplicationUseCase);\nconst viewModel = container.resolve<IViewModel>(IViewModel)\n\nconst props = defineProps<IAddOrUpdateAppPropsInline>();\nconst name = ref(props.app?.name);\nconst isAppNameValid = ref<boolean>(false);\nconst isDescriptionValid = ref<boolean>(true);\nconst description = ref(props.app?.description);\nconst selectedOrgGroup = ref(viewModel.user?.selectedOrganizationGroup?.organizationGroup)\nconst emit = defineEmits([\"close\"]);\n\nconst validations = ref<Record<\"name\" | \"description\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 3, max: 25 }, availableCharacters: true, nonSpaceCharacter: true },\n    description: { minAndMaxLength: { min: 3, max: 100 }, availableCharacters: false, nonSpaceCharacter: false },\n});\n\nfunction addOrUpdateApplication() {\n    executor.execute(async () => {\n        emit(\"close\");\n        if (props.app) {\n            await applicationUseCase.updateApplication({ ID: props.app.ID, name: name.value, description: description.value, updateReason: \"userUpdate\" });\n        } else {\n            await applicationUseCase.createApplication({ name: name.value!, description: description.value, templateName: props.templateName, organizationGroupId: props.organizationGroupId });\n        }\n        name.value = description.value = \"\";\n    }, { loading: selectedOrgGroup.value?.mainOrganizationId ? false : true , noContinueInBgBtn: true});\n}\n\nfunction appNameValidationStatus(isValid: boolean) {\n    isAppNameValid.value = isValid\n\n}\nconst isThereUpdate = () => {\n    if(props.app) {\n        return !(props.app!.name == name.value && props.app!.description == description.value)\n    }\n    else {\n        return true;\n    }\n}\nfunction descriptionValidationStatus(isValid: boolean) {\n    isDescriptionValid.value = isValid\n\n}\n\nfunction setNameValue(value: string) {\n    name.value = value\n}\n\nfunction setDescValue(desc: string) {\n    description.value = desc\n}\nfunction close() {\n    emit(\"close\");\n}\n\nconst addButtonDisabled = computed(() => !isAppNameValid.value || !isDescriptionValid.value || !isThereUpdate());\n\n</script>\n\n<template>\n    <div class=\"container\">\n        <div class='updateOrCreateTextAreaDiv'>\n            <IDEInput :isRequired=\"true\" elementId=\"nameInput\" @getValue=\"setNameValue\" @validateStatus=\"appNameValidationStatus\" placeholder=\"\"\n                :type=\"'text'\" :value=\"props.app?.name\"  info-text=\"Application Name\" :validations=\"validations.name\" />\n        </div>\n        <div class='updateOrCreateInputDiv'>\n            <IDEInput :isRequired=\"false\" elementId=\"descriptionInput\" @validateStatus=\"descriptionValidationStatus\" placeholder=\"\" \n                :type=\"'textarea'\" :value=\"props.app?.description\"   info-text=\"Description\" :validations=\"validations.description\" @getValue=\"setDescValue\" />\n        </div>\n        <div class='updateOrCreateRow'>\n            <div class=\"col\">\n                <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n                <button :style=\"{ color: addButtonDisabled ? '#757575' : '#FFFFFF' }\" class='updateOrCreateBtn'\n                    @click=\"addOrUpdateApplication()\" :disabled=\"addButtonDisabled\">{{\n                            app ? \"SAVE\" : \"CREATE\"\n                    }}</button>\n            </div>\n        </div>\n    </div>\n\n\n\n</template>\n\n<style  scoped>\n* {\n    font-family: \"Roboto\";\n}\n.container {\n    padding: 32px 20px 20px\n}\n.updateOrCreateTextAreaDiv {\n    height: 77px;\n}\n\n.appInfo {\n    color: #151233;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    opacity: 1;\n\n}\n\n\n\n.updateOrCreateTextarea {\n    width: 100%;\n    outline: none;\n    height: 120px;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    resize: none;\n    margin-top: 3px;\n}\n\n.updateOrCreateInputDiv {\n    height: 173px;\n\n}\n\n.updateOrCreateInput {\n    width: 100%;\n    outline: none;\n    height: 40px;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    margin-top: 3px;\n}\n\n.updateOrCreateRow {\n    display: flex;\n    text-align: end;\n}\n\n.updateOrCreateBtn {\n    background-color: #151233;\n    color: #fff;\n    border-radius: 4px;\n    /* width: 72px; */\n    height: 40px;\n    font-weight: 500;\n    font-size: 16px;\n    padding: 8px 15px;\n}\n.cancel-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #757575;\n    padding: 8px 16px;\n    border-radius: 4px;\n    margin-right: 4px;\n    border: none;\n    background: transparent;\n}\n::placeholder {\n    padding-left: 10px;\n    padding-top: 5px;\n    color: #A1A1A1;\n    font-style: normal;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    font-size: 14px;\n    opacity: 1px;\n}\n</style>"],"sourceRoot":""}]);
